export default {
	data() {
		return {
			nickName: '', //用户姓名
			userName: '', //登录账号
		};
	},
	methods: {
		InformationQuery() {
			//个人信息查询
			let userUUID = uni.getStorageSync('wx_uuid')
			this.$http.get(this.$store.state.operatorSelect, {
				uuid: userUUID
			}).then(res => {
				console.log(res)
				this.nickName = res.data.data.nickname
				this.userName = res.data.data.username
			})
		}

	},
	created: function() {
		this.InformationQuery()
		console.log(66)
	}

}
